.alpheios-alignment-editor-align-define-container-view-mode {
  margin-top: 15px;
  border: 1px solid #ddd;
  border-bottom-color: transparent;
  background: #f3f3f3;
}
.alpheios-alignment-editor-align-define-container-view-mode .alpheios-alignment-editor-align-segment-data {
  border-bottom: 2px solid #ddd;
}
.alpheios-alignment-editor-align-define-container-view-mode .alpheios-alignment-editor-align-segment-data:before, .alpheios-alignment-editor-align-define-container-view-mode .alpheios-alignment-editor-align-segment-data:after {
  clear: both;
  display: table;
  content: "";
