.alpheios-alignment-editor-align-text-segment span {
  cursor: pointer;
  padding: 4px;
  border: 1px solid transparent;
  display: inline-block;
}
.alpheios-alignment-editor-align-text-segment span:hover {
  border-color: #FFC24F;
  background: #FFD27D;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token-grouped {
  border-color: #BCE5F0;
  background: #BCE5F0;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token-selected {
  border-color: #F27431;
  background: #F27431;
  color: #fff;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token-clicked {
  border-color: #f59d6e;
  background: #f59d6e;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token-clicked-first {
  border-color: #f06d26;
  background: #f06d26;
  color: #fff;
}