.alpheios-alignment-editor-text-define-container-inner:before, .alpheios-alignment-editor-text-define-container-inner:after {
  content: "";
  display: block;
  clear: both;
}

.alpheios-alignment-editor-text-define-container .alpheios-alignment-editor-text-container {
  width: 50%;
  float: left;
}

.alpheios-alignment-editor-origin-text-container {
  padding-right: 10px;
}

.alpheios-alignment-editor-target-text-container {
  padding-left: 10px;
}

.alpheios-alignment-editor-container-footer {
  text-align: center;
  border-top: 2px solid #757575;
  padding-top: 15px;
}

.alpheios-alignment-editor-text-define-container__show-label {
  cursor: pointer;
  font-size: 90%;
  text-decoration: underline;
  color: #185F6D;
}