.alpheios-select {
  font-family: "Source Sans Pro", sans-serif;
  box-sizing: border-box;
  margin: 0;
  border-radius: 10px;
  font: inherit;
  text-transform: none;
  max-width: 100%;
  width: 100%;
  border: 1px solid #757575;
  padding: 8px 10px;
  background: #fff;
  color: #757575;
  -webkit-appearance: none;
}
.alpheios-select:not([multiple]):not([size]) {
  height: 42px;
  vertical-align: middle;
  display: inline-block;
}
.alpheios-select:not(select) {
  line-height: 42px;
}
.alpheios-select[multiple], .alpheios-select[size] {
  padding-top: 4px;
  padding-bottom: 4px;
  vertical-align: top;
}
.alpheios-select:focus {
  outline: none;
}
.alpheios-select:disabled {
  background-color: #fff;
  color: #909090;
}
.alpheios-select:not([multiple]):not([size]) {
  -webkit-appearance: none;
  -moz-appearance: none;
  padding-right: 20px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2224%22%20height%3D%2216%22%20viewBox%3D%220%200%2024%2016%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23666%22%20points%3D%2212%201%209%206%2015%206%22%20%2F%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23666%22%20points%3D%2212%2013%209%208%2015%208%22%20%2F%3E%0A%3C%2Fsvg%3E%0A");
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-size: 24px;
}
.alpheios-select:not([multiple]):not([size])::-ms-expand {
  display: none;
}
.alpheios-select:not([multiple]):not([size]) option {
  color: #757575;
}
.alpheios-select:not([multiple]):not([size]):disabled {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2224%22%20height%3D%2216%22%20viewBox%3D%220%200%2024%2016%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23999%22%20points%3D%2212%201%209%206%2015%206%22%20%2F%3E%0A%20%20%20%20%3Cpolygon%20fill%3D%22%23999%22%20points%3D%2212%2013%209%208%2015%208%22%20%2F%3E%0A%3C%2Fsvg%3E%0A");
}

input.alpheios-input, input.alpheios-input[type] {
  box-sizing: border-box;
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
  font-size: inherit;
  max-width: 100%;
  width: 100%;
  border: 1px solid #757575;
  border-radius: 10px;
  padding: 8px;
  background: #fff;
  color: #757575;
  vertical-align: middle;
  display: inline-block;
  -webkit-appearance: none;
}
input.alpheios-input[type=search]::-webkit-search-cancel-button, input.alpheios-input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input.alpheios-input[type=number]::-webkit-inner-spin-button, input.alpheios-input[type=number]::-webkit-outer-spin-button {
  height: auto;
}
input.alpheios-input::-moz-placeholder {
  opacity: 1;
}
input.alpheios-input:not(input) {
  line-height: 40px;
}
input.alpheios-input:focus, input.alpheios-input[type]:focus {
  outline: none;
  border: inherit;
  border: 1px solid #757575;
  border: 1px solid #757575;
  border-radius: 10px;
}
input.alpheios-input:disabled {
  background-color: #e3e3e3;
  color: #909090;
  border: 1px solid #757575;
  border-radius: 10px;
}
input.alpheios-input::placeholder {
  color: #fff;
}
input.alpheios-input::-ms-input-placeholder {
  color: #fff !important;
}

.alpheios-button-tertiary, button.alpheios-button-tertiary, .alpheios-button-primary, .alpheios-button {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  font-weight: 700;
  margin: 0;
  border: 1px solid transparent;
  overflow: visible;
  white-space: nowrap;
  display: inline-block;
  box-sizing: border-box;
  padding: 0.6em 1.2em;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  line-height: 1;
  border-radius: 0.6em;
  cursor: pointer;
}
.alpheios-button-tertiary::-moz-focus-inner, .alpheios-button-primary::-moz-focus-inner, .alpheios-button::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.alpheios-button-tertiary:hover, .alpheios-button-primary:hover, .alpheios-button:hover {
  text-decoration: none;
}
.alpheios-button-tertiary:focus, .alpheios-button-primary:focus, .alpheios-button:focus {
  outline: none;
}

.alpheios-button-primary {
  color: #FFF;
  background-color: #C64906;
  border-color: #F27431;
}
.alpheios-button-primary:hover {
  color: #FFF;
  background-color: #F27431;
  border-color: #F27431;
  box-shadow: 0 0.25em 0.25em rgba(0, 0, 0, 0.25);
}
.alpheios-button-primary:active {
  color: #FFF;
  background-color: #F27431;
  border-color: #F27431;
  box-shadow: inset 0 0.25em 0.25em rgba(0, 0, 0, 0.1);
}
.alpheios-button-primary:disabled {
  color: #fff;
  background: #f8b998;
  border-color: #f8b998;
  box-shadow: inset 0 0.25em 0.25em rgba(0, 0, 0, 0.1);
}

.alpheios-button-tertiary, button.alpheios-button-tertiary {
  color: #185F6D;
  background: #fff;
  border-color: #185F6D;
}
.alpheios-button-tertiary:hover, button.alpheios-button-tertiary:hover {
  color: #fff;
  background: #185F6D;
  border-color: #185F6D;
  box-shadow: 0 0.25em 0.25em rgba(0, 0, 0, 0.25);
}
.alpheios-button-tertiary:active, button.alpheios-button-tertiary:active {
  color: #fff;
  background: #185F6D;
  border-color: #185F6D;
  box-shadow: inset 0 0.25em 0.25em rgba(0, 0, 0, 0.1);
}
.alpheios-button-tertiary:disabled, button.alpheios-button-tertiary:disabled {
  color: #fff;
  background: #8bafb6;
  border-color: #8bafb6;
  box-shadow: inset 0 0.25em 0.25em rgba(0, 0, 0, 0.1);
}

.alpheios-alignment-app-container {
  font-family: "Source Sans Pro", sans-serif;
}
.alpheios-alignment-app-container * {
  box-sizing: border-box;
